import { Component, ViewChild } from '@angular/core';
import { SharedModule } from '@shared/shared.module';
import { ButtonModule } from '@shared/components/button/button.module';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AppEmailValidator } from '@core/validators';
import { MatFormFieldModule } from '@angular/material/form-field';
import { VALIDATOR } from '@core/constants';
import { MatInputModule } from '@angular/material/input';
import { MatAutocompleteModule } from '@angular/material/autocomplete';
import { MatSelectModule } from '@angular/material/select';
import { MatDividerModule } from '@angular/material/divider';
import { ReactiveFormsModule } from '@angular/forms';
import { MatInput } from '@angular/material/input';
import { MatIconModule } from '@angular/material/icon';
import { MatDialog } from '@angular/material/dialog';
import { EnquirySentDialogComponent } from '@feature/agent/components/enquiry-sent-dialog/enquiry-sent-dialog.component';
import { SnackService } from '@core/services/snack.service';
import { ViewportService } from '@core/services/viewport.service';
import { SellerLeadFacade } from '../sellerLead.facade';
import { RouterModule } from '@angular/router';
@Component({
    selector: 'findqo-sign-up-section-page',
    templateUrl: './sign-up-section.page.html',
    styleUrls: ['./sign-up-section.page.scss'],
    imports: [
        ReactiveFormsModule,
        SharedModule,
        MatDividerModule,
        ButtonModule,
        MatFormFieldModule,
        MatIconModule,
        MatInputModule,
        MatAutocompleteModule,
        MatSelectModule,
        RouterModule,
    ]
})
export class SignUpSectionPage {
	constructor(
		public viewport: ViewportService,
		private snack: SnackService,
		private dialog: MatDialog,
		private fb: FormBuilder,
		private sellerLeadFacade: SellerLeadFacade
	) {}
	@ViewChild('phoneNumber') phoneNumber?: MatInput;

	form: FormGroup = this.fb.group({
		companyName: [''],
		email: ['', [Validators.required, AppEmailValidator.validateEmailPattern()]],
		name: ['', [Validators.required, VALIDATOR.pattern.name, Validators.minLength(3)]],
		phone: ['', [Validators.required, Validators.maxLength(10), Validators.pattern('^[0-9]*$'), Validators.minLength(7)]],
	});

	submitLoading = false;

	onClickLink(): void {
		const headerOffset = 125;
		const element = document.getElementById('pricingList');
		window.scrollTo({
			top: element.offsetTop - headerOffset,
			behavior: 'smooth',
		});
	}

	onSubmit(): void {
		if (this.form.invalid || this.submitLoading) return;

		this.submitLoading = true;

		const FORM = this.form.getRawValue();

		const PAYLOAD = {
			...FORM,
			enquiredBy: FORM.name,
			name: FORM.companyName,
		};

		delete PAYLOAD.companyName;

		this.sellerLeadFacade.singupAgentV2(PAYLOAD).subscribe({
			next: () => {
				this.submitLoading = false;
				this.form.reset();
				this.form.get('name').setErrors(null);
				this.form.get('email').setErrors(null);
				this.form.get('phone').setErrors(null);

				this.dialog.open(EnquirySentDialogComponent, {
					width: '100%',
					maxWidth: '500px',
					height: 'auto',
				});
			},
			error: () => {
				this.submitLoading = false;
				this.snack.open({
					data: { translationKey: 'n608' },
					panelClass: ['snackbar-error'],
				});
			},
		});
	}
	mouseClick(): void {
		setTimeout(() => {
			this.phoneNumber?.focus();
		}, 200);
	}
}
