import { Injectable } from '@angular/core';
import { AgentsApi } from '@core/data-access/apis/agents.api';
import { AgentSignUpBodyV2 } from '@core/models';
import { Observable } from 'rxjs';

@Injectable({
	providedIn: 'root',
})
export class SellerLeadFacade {
	constructor(private readonly agentApi: AgentsApi) {}

	singupAgentV2(body: AgentSignUpBodyV2): Observable<object> {
		return this.agentApi.agentSignUp(body);
	}
}
